:root{--timer-font-size: 40px;--timer-margin-bottom: 15px;--timer-input-margin: 15px}.timer{display:flex;flex-direction:column;align-items:center;padding-top:10px;padding-bottom:10px}.timer__output{margin-bottom:var(--timer-margin-bottom);font-size:var(--timer-font-size);color:var(--color-gray)}.timer__input{min-width:var(--elements-width);height:calc(var(--elements-height) - 15px);margin-bottom:var(--timer-input-margin);padding:var(--input-padding);transition:box-shadow .3s ease-in-out}.timer__start,.timer__stop{justify-content:flex-start;padding-left:58px}.timer__start::after,.timer__stop::after{content:"";margin-left:auto;margin-right:30px}.timer__start{margin-bottom:var(--timer-margin-bottom)}.timer__start::after{content:"➤"}.timer__stop::after{content:"✖"}